import React from 'react';
import { connect } from 'react-redux';
import { NavBar, SearchBar, ActivityIndicator, Toast } from 'antd-mobile';
import Content from '../../views/common/Content';
import NativeAppScreen from '../common/NativeAppScreen';
import ReactDOM from 'react-dom';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { NATIVE_MESSAGE_TYPE } from '../../core/utils/IConstant';
import Config from '../../core/utils/Config';
import {
    goBackAction,
    isPanelShowingChangeAction,
    isFetchingChangeAction,
    keywordChangeAction,
    mapZoomAndCenterLocationChangeAction,
    searchTypeChangeAction,
    asyncGetListAction,
    selectedItemChangeAction,
} from '../../store/actions/map/elementMapAction';
import { setConfigItemAction } from '../../store/actions/nativeAction';
import '../../assets/css/map.css';
import LeftContentView from '../../views/common/LeftContentView';
import * as IConstant from '../../core/utils/IConstant';
import SysUtil from '../../core/utils/SysUtil';

const MAP_ELEMENT_TYPES = [
    {
        title: '出行必看',
        list: [
            { name: '景区', type: IConstant.ItemType.You },
            { name: '酒店', type: IConstant.ItemType.Zhu },
            { name: '餐饮', type: IConstant.ItemType.CanTing },
            { name: '购物', type: IConstant.ItemType.Gou },
            { name: '娱乐', type: IConstant.ItemType.Yu },
        ]
    }/*,
    {
        title: '生活助手',
        list: [
            { name: '公厕', type: IConstant.ItemType.Toilet },
            { name: '银行', type: IConstant.ItemType.Bank },
            { name: '医院', type: IConstant.ItemType.Hopsital },
            { name: '邮局', type: IConstant.ItemType.PostOffice },
            { name: '加油站', type: IConstant.ItemType.Gasstation },
        ]
    }*/
];

class ElementMapScreen extends NativeAppScreen 
{
    markerClusterer;
    constructor(props) {
        super(props);
        this.state = {
            mapHeight: 0,
            panelBottom: 0,
        };

        this.deSelectPoi = this.deSelectPoi.bind(this);
        this.searchTypeButtonTapped = this.searchTypeButtonTapped.bind(this);
        this.hidePanel = this.hidePanel.bind(this);
        this.togglePanel = this.togglePanel.bind(this);
        this.searchBarFocus = this.searchBarFocus.bind(this);
        this.dailPhone = this.dailPhone.bind(this);
        this.navTo = this.navTo.bind(this);
        this.showDetails = this.showDetails.bind(this);
    }
    componentDidMount() 
    {
        this.props.dispatch(isFetchingChangeAction(true));
        this.updateWindowDimensions();
    }

    componentDidUpdate(prevProps, prevState, snapshot)
    {
        let isUpdateLocation = !prevProps.baiduMapInited && this.props.baiduMapInited;
        isUpdateLocation = isUpdateLocation || (!prevProps.lat && this.props.lat !== prevProps.lat);
        if (isUpdateLocation) 
        {
            this.updateFromLocation(this.props);
        }

        if (this.props.searchType !== prevProps.searchType && this.props.searchType !== undefined) 
        {
            this.props.dispatch(asyncGetListAction(this.props.searchType, this.props.keywords, this.props.lat, this.props.lng));
        }

        if (this.props.dataList !== prevProps.dataList) 
        {
            this.addMarkers(this.props.dataList, this.props.searchType);
        }
        if(this.props.height && !prevProps.height)
        {
            this.updateWindowDimensions();
        }
        // native端搜索
        if(this.props.nativeSearchKeywords !== prevProps.nativeSearchKeywords)
        {
            this.updateKeyword(this.props.nativeSearchKeywords);
            this.searchByKeyword(this.props.nativeSearchKeywords);
        }
        // native 想要获取当前的数据列表
        if(this.props.getElementMapDataList !== prevProps.getElementMapDataList && this.props.getElementMapDataList === 0)
        {
            this.props.dispatch(setConfigItemAction({getElementMapDataList: 1}));
            this.sendCommonDataList();
        }
    }

    render() {
        return (
            <div className={ this.props.isFromNativeApp ? 'isFromNativeApp elementMapPage' : 'elementMapPage' }>
                {
                    this.props.isFromNativeApp
                        ? null
                        :
                        <NavBar mode="dark"
                            className="navBar"
                            leftContent={<LeftContentView
                                ref={(view) => { this.LeftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                                goBackAction={goBackAction}
                                history={this.props.history}
                            />}
                            onLeftClick={() => {
                                this.LeftContentView.goHome();
                            }}>
                            <SearchBar placeholder="请输入关键字"
                                maxLength={20}
                                value={this.props.keywords === undefined ?  '' : this.props.keywords}
                                onFocus={() => { this.searchBarFocus(); }}
                                disabled={ this.isEntitySearchType() ? true : false }
                                onChange={(val) => { this.updateKeyword(val); }}
                                onCancel={(val) => {
                                    this.updateKeyword('');
                                    this.searchByKeyword('');
                                }}
                                onSubmit={(val) => {
                                    this.updateKeyword(val);
                                    this.searchByKeyword(val);
                                }} />
                        </NavBar>
                }
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}}>
                    <div id="elementMap" ref={(view) => { this.mapView = view; }} style={{ height: (this.state.mapHeight + 'px') }}></div>
                    <CSSTransitionGroup
                        transitionName="elementMapPanel"
                        transitionAppearTimeout={500}
                        transitionEnterTimeout={500}
                        transitionLeaveTimeout={300}>
                        {
                            this.props.isPanelShowing
                                ?
                                <div key="1" className="mapTypePanel" onClick={this.hidePanel}>
                                    {
                                        this.renderPanelDetails()
                                    }
                                </div>
                                :
                                null
                        }
                    </CSSTransitionGroup>
                    {
                        this.renderFooterDetails()
                    }
                    {
                        this.renderSegmentButtonView()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator
                                toast
                                text="Loading..." />
                            : null
                    }
                </Content>
            </div>
        );
    }
    renderSegmentButtonView() {
        let searchTypeName = this.getSearchTypeName();
        return searchTypeName && searchTypeName.length > 0 ? (
            <div className="searchTypeName" onClick={() => this.togglePanel()}>{searchTypeName}</div>
        ) : null;
    }

    renderPanelDetails() {
        return (
            <div style={{ position: 'absolute', top:'0px' ,width: '100%', overflow: 'scroll', backgroundColor: '#fff' }}>
                <div style={{ overflow:'scroll', left:'0',right:'0' }}>
                    {
                        MAP_ELEMENT_TYPES.map((typeItem, sectionIndex) => <div key={typeItem.title} className="typeBoxView">
                            <div className="typeName">{typeItem.title}</div>
                            <div className="typeView">
                                {
                                    typeItem.list.map((item, rowIndex) => <div key={item.type} onClick={() => this.searchTypeButtonTapped(item.type)} className="typeButton">
                                        {item.name}
                                    </div>)
                                }
                            </div>
                        </div>)
                    }
                </div>
            </div>
        );
    }

    renderFooterDetails()
    {
        if (!this.props.selectedItem)
        {
            return null;
        }
        let phoneIcon = require('../../assets/images/map/ico_map_phone@2x.png');
        let navIcon = require('../../assets/images/map/ico_map_go@2x.png');
        let detailsIcon = require('../../assets/images/map/ico_map_detail@2x.png');
        return (
            <div className="footerDetails">
                <div className="footerDetailsName">{this.props.selectedItem.name}</div>
                <div className="footerDetailsAddress">{this.props.selectedItem.distance}&nbsp;&nbsp;&nbsp;&nbsp;{this.props.selectedItem.address}</div>
                <div className="footerDetailsButtons">
                    <div className={this.props.selectedItem.telephone ? 'footerDetailsButton' : 'footerDetailsButton footerDetailsButtonDisabled'}
                        onClick={this.dailPhone}>
                        <img src={phoneIcon} alt="" /> 电话
                    </div>
                    <div className="footerDetailsSplitLine" />
                    <div className={this.props.selectedItem.latitude && this.props.selectedItem.longitude  ? 'footerDetailsButton' : 'footerDetailsButton footerDetailsButtonDisabled'}
                        onClick={this.navTo}>
                        <img src={navIcon} alt="" /> 到这去
                    </div>
                    <div className="footerDetailsSplitLine" />
                    <div className={ !this.isEntitySearchType() ?  'footerDetailsButton' :  'footerDetailsButton footerDetailsButtonDisabled'}
                        onClick={this.showDetails}>
                        <img src={detailsIcon} alt="" /> 详情
                    </div>
                </div>
            </div>
        );
    }

    initMap() {
        // 地图标记图片
        this.markerIcon = new window.BMap.Icon(require('../../assets/images/map/common_poi@2x.png'), new window.BMap.Size(30, 30), {
            anchor: new window.BMap.Size(15, 30),
            imageSize: new window.BMap.Size(30, 30)
        });

        this.selectedMarkerIcon = new window.BMap.Icon(require('../../assets/images/map/common_poi_selected@2x.png'), new window.BMap.Size(30, 30), {
            anchor: new window.BMap.Size(15, 30),
            imageSize: new window.BMap.Size(30, 30)
        });

        this.map = this.map ? this.map : new window.BMap.Map('elementMap');
        this.map.centerAndZoom(new window.BMap.Point(Config.instance.appCityLongitude, Config.instance.appCityLatitude), 16);
        this.map.addControl(new window.BMap.NavigationControl({ type: window.BMAP_NAVIGATION_CONTROL_SMALL, anchor: window.BMAP_ANCHOR_BOTTOM_RIGHT, enableGeolocation: true, offset: new window.BMap.Size(20, 20) }));
        this.map.addControl(new window.BMap.GeolocationControl({ enableAutoLocation: false }));

        this.map.addEventListener('click', this.deSelectPoi);
        this.map.addEventListener('dragstart', this.deSelectPoi);
    }

    updateWindowDimensions() 
    {
        const height = window.innerHeight - ReactDOM.findDOMNode(this).parentNode.offsetTop;
        this.setState({ mapHeight: height, panelBottom: (window.innerHeight / 3.0) }, () => {
            this.updateFromLocation(this.props);
        });
    }

    updateFromLocation(props) 
    {
        if (props.baiduMapInited)
        {
            this.initMap();
        }
        if (props.baiduMapInited && props.lat && props.lng && (!props.searchType || props.searchType.length <= 0 )) {
            let searchType = IConstant.ItemType.You;
            this.props.dispatch(searchTypeChangeAction(searchType));
        }
        else if (props.dataList && props.searchType)
        {
            this.addMarkers(props.dataList,props.searchType);
            this.map.setZoom(props.mapZoom);
            this.map.setCenter(new window.BMap.Point(props.centerLng, props.centerLat));
        }
    }

    // 更新keyword
    updateKeyword(val) 
    {
        this.props.dispatch(keywordChangeAction(val));
    }

    // 搜索 keyword
    searchByKeyword(val) 
    {
        this.props.dispatch(asyncGetListAction(this.props.searchType, val, this.props.lat, this.props.lng));
    }

    //搜索框 focus 
    searchBarFocus()
    {
        this.deSelectPoi();
        this.hidePanel();
        if (this.isEntitySearchType())
        {
            Toast.success('暂只支持景区，酒店，餐饮，购物，娱乐的关键字查询！',2);
            return;
        }
    }

    // 是否是 其它类型 的数据
    isEntitySearchType()
    {
        let searchType = this.props.searchType;
        return !(searchType === IConstant.ItemType.You || searchType === IConstant.ItemType.Zhu || searchType === IConstant.ItemType.CanTing || searchType === IConstant.ItemType.Gou || searchType === IConstant.ItemType.Yu);
    }

    togglePanel() 
    {
        this.deSelectPoi();
        this.props.dispatch(isPanelShowingChangeAction(!this.props.isPanelShowing));
    }
   
    hidePanel() 
    {
        this.deSelectPoi();
        this.props.dispatch(isPanelShowingChangeAction(false));
    }

    addMarkers(dataList, searchType) 
    {
        this.props.dispatch(isFetchingChangeAction(false));
        this.map.clearOverlays();
        if (!this.map || !dataList || dataList.length === 0) 
        {
            return;
        }
        this.markers = [];
        let pts = [];
        for (let i = 0; i < dataList.length; i++) 
        {
            let item = dataList[i];
            let pt;
            if (searchType === IConstant.ItemType.You || searchType === IConstant.ItemType.Zhu || searchType === IConstant.ItemType.CanTing || searchType === IConstant.ItemType.Gou || searchType === IConstant.ItemType.Yu )
            {
                pt = new window.BMap.Point(item.Longitude, item.Latitude);
            }
            else
            {
                pt = new window.BMap.Point(item.longitude, item.latitude);
            }

            let marker = new window.BMap.Marker(pt, { icon: this.markerIcon });
            marker.addEventListener('click', function (e) {
                e.domEvent.stopPropagation();
                this.markerTapped(i);
            }.bind(this));

            pts.push(pt);
            this.markers.push(marker);
        }
        if (!this.markerClusterer) 
        {
            this.markerClusterer = new window.BMapLib.MarkerClusterer(this.map);
        }
        this.markerClusterer.clearMarkers(); //清空 聚合mark 
        this.markerClusterer.addMarkers(this.markers);
        this.map.setViewport(pts);
    }
    deSelectPoi() 
    {
        this.props.dispatch(selectedItemChangeAction(null));

        if (this.markers && this.markers.length > 0)
        {
            for (let i = 0; i < this.markers.length; i++) 
            {
                let marker = this.markers[i];
                if (marker.getIcon() !== this.markerIcon) 
                {
                    marker.setIcon(this.markerIcon);
                }
            }
            this.updateMapControlPosition(80);
        }
    }

    updateMapControlPosition(bottom = 140) 
    {
        let mapCtrls = this.mapView.getElementsByClassName('BMap_noprint');
        for (let i = 0; i < mapCtrls.length; i++) 
        {
            let ctrl: any = mapCtrls[i];
            ctrl.style.cssText += 'bottom: ' + bottom + 'px;';
        }
    }
    markerTapped(index) 
    {
        this.updateMapControlPosition();
        for (let i = 0; i < this.markers.length; i++) 
        {
            let marker = this.markers[i];
            if (index === i) 
            {
                marker.setIcon(this.selectedMarkerIcon);
            }
            else if (marker.getIcon() !== this.markerIcon) 
            {
                marker.setIcon(this.markerIcon);
            }
        }
        let selectedItem = this.elementItemToCommonItem(this.props.dataList[index]);
        this.props.dispatch(selectedItemChangeAction(selectedItem));
    }

    searchTypeButtonTapped(searchType) 
    {
        this.deSelectPoi();
        if (this.props.searchType !== searchType) 
        {
            //清空关键字
            if (this.props.keywords && this.props.keywords.length > 0)
            {
                this.props.dispatch(keywordChangeAction('')).then( () => {
                    return this.props.dispatch(searchTypeChangeAction(searchType));
                });
            }
            else
            {
                this.props.dispatch(searchTypeChangeAction(searchType));
            }
        }
    }

    // 保存 地图中心点 和 缩放级
    setMapZoomAndCenterLocation()
    {
        if (!this.map)
        {
            return;
        }
        let centerPoint = this.map.getCenter();
        let mapZoom = this.map.getZoom();
        let centerLat = centerPoint.lat;
        let centerLng = centerPoint.lng;
        this.props.dispatch(mapZoomAndCenterLocationChangeAction(mapZoom,centerLat,centerLng));
    }

    // 电话
    dailPhone()
    {
        this.setMapZoomAndCenterLocation();
        let detailItem = this.props.selectedItem;
        if ( detailItem.telephone && detailItem.telephone.length > 0 )
        {
            if(this.props.isFromNativeApp && window.postMessage)
            {
                this.sendMessageToNative(NATIVE_MESSAGE_TYPE.DIAL, {
                    phoneNumber: detailItem.telephone,
                });
            }
            else
            {
                SysUtil.openTel(detailItem.telephone);
            }
        }
    }

    // 导航
    navTo()
    {
        this.setMapZoomAndCenterLocation();
        let detailItem = this.props.selectedItem;
        if (detailItem.latitude && detailItem.latitude > 0 && detailItem.longitude && detailItem.longitude > 0)
        {
            //导航点击
            if(this.props.isFromNativeApp && window.postMessage)
            {
                this.sendMessageToNative(NATIVE_MESSAGE_TYPE.MAP_NAV, {
                    code: detailItem.code,
                    toLat: detailItem.latitude,
                    toLng: detailItem.longitude,
                });
            }
            else
            {
                this.props.history.push(`/map/mapNav/${detailItem.code}/${detailItem.latitude}/${detailItem.longitude}`);
            }
        }
    }

    // 详情
    showDetails()
    {
        if (this.isEntitySearchType())
        {
            return;
        }

        this.setMapZoomAndCenterLocation();
        let detailItem = this.props.selectedItem;
        let searchType = this.props.searchType;
        if(this.props.isFromNativeApp && window.postMessage)
        {
            this.sendMessageToNative(NATIVE_MESSAGE_TYPE.ELEMENT_DETAIL, {
                type: searchType,
                imageUrl: detailItem.imageUrl,
                name: detailItem.name,
                code: detailItem.code,
            });
        }
        else
        {
            if (searchType === IConstant.ItemType.You)
            {
                this.props.history.push(`/scenic/detail/${detailItem.code}`);
            }
            else if ( searchType === IConstant.ItemType.Zhu )
            {
                this.props.history.push(`/hotel/detail/${detailItem.code}`);
            }
            else if ( searchType === IConstant.ItemType.CanTing )
            {
                this.props.history.push(`/restaurant/detail/${detailItem.code}`);
            }
            else if ( searchType === IConstant.ItemType.Gou )
            {
                this.props.history.push(`/shopping/detail/${detailItem.code}`);
            }
            else if ( searchType === IConstant.ItemType.Yu )
            {
                this.props.history.push(`/fun/detail/${detailItem.code}`);
            }
        }
    }

    getSearchTypeName()
    {
        let searchType = this.props.searchType;
        let searchTypeName = '';
        for (let i = 0; i < MAP_ELEMENT_TYPES.length; i++) 
        {
            let list = MAP_ELEMENT_TYPES[i].list;
            for (let j = 0; j < list.length; j++) 
            {
                let item = list[j];
                if (item.type === searchType) 
                {
                    searchTypeName = item.name;
                    break;
                }
            }
            if (searchTypeName && searchTypeName.length > 0) 
            {
                break;
            }
        }
        return searchTypeName;
    }

    elementItemToCommonItem(item)
    {
        let name;
        let code;
        let distance;
        let telephone;
        let address;
        let latitude;
        let longitude;
        let imageUrl;
        let searchType = this.props.searchType;
        let searchTypeName = this.getSearchTypeName();
        item.Distance = item.Distance ? item.Distance : item.distance;
        item.distance = item.distance ? item.distance : item.Distance;

        if (searchType === IConstant.ItemType.You || searchType === IConstant.ItemType.Zhu) 
        {
            name = item.CnName;
            code = item.Code;
            distance = item.Distance ? parseFloat(item.Distance).toFixed(1) + '千米' : '';
            telephone = item.Telephone ? item.Telephone : '';
            address = item.Address && item.Address.length > 0 ? item.Address : '地址暂无';
            latitude = item.Latitude;
            longitude = item.Longitude;
            if (item.GetImage) 
            {
                imageUrl = item.GetImage.ImageFrontUrl;
            }
        }
        else if ( searchType === IConstant.ItemType.CanTing || searchType === IConstant.ItemType.Gou || searchType === IConstant.ItemType.Yu )
        {
            name = item.CnName;
            code = item.Code;
            distance = item.distance ? item.distance.toFixed(1) + '千米' : '';
            telephone = item.Telephone ? item.Telephone : '';
            address = item.Address && item.Address.length > 0 ? item.Address : '地址暂无';
            latitude = item.Latitude;
            longitude = item.Longitude;
            if (item.GetImage) 
            {
                imageUrl = item.GetImage.SmallImageFrontUrl;
            }
        }
        else
        {
            name = item.CnName;
            code = item.Code;
            distance = item.distance ? item.distance.toFixed(1) + '千米' : '';
            telephone = null;
            address = item.Address && item.Address.length > 0 ? item.Address :  ( searchTypeName.replace('/',',') + '  地址暂无') ;
            latitude = item.Latitude;
            longitude = item.Longitude;
            if (item.GetImage) 
            {
                imageUrl = item.GetImage.SmallImageFrontUrl;
            }
        }
        let selectedItem = {
            name: name,
            code: code,
            distance: distance,
            telephone: telephone,
            address: address,
            latitude: latitude,
            longitude: longitude,
            imageUrl: imageUrl,
            type: searchType
        };
        return selectedItem;
    }
    dataListToCommonList()
    {
        if(!this.props.dataList || this.props.dataList.length === 0)
        {
            return [];
        }
        let _dataList = [];
        for(let i = 0; i < this.props.dataList.length; i++)
        {
            let item = this.props.dataList[i];
            _dataList.push(this.elementItemToCommonItem(item));
        }
        return _dataList;
    }

    sendCommonDataList()
    {
        if(window.postMessage)
        {
            window.postMessage(JSON.stringify({
                type: NATIVE_MESSAGE_TYPE.ELEMENT_MAP_DATALIST,
                data: {
                    dataList: this.dataListToCommonList(),
                    type: this.props.searchType,
                    title: this.getSearchTypeName(),
                }
            }), '*');
        }
    }
}

const mapStateToProps = function (state, ownProps) {
    let obj = state.map.elementMap;
    return {
        ...obj,
        baiduMapInited: state.system.baiduMapInited,
        markerClusterInited: state.system.markerClusterInited,
        lat: state.system.latitude,
        lng: state.system.longitude,
        isFromNativeApp: state.native.isFromNativeApp,
        ...state.native.windowSize,
        nativeSearchKeywords: state.native.elemantMapSearchKeywords,
        getElementMapDataList: state.native.getElementMapDataList,
    };
};

ElementMapScreen = connect(mapStateToProps)(ElementMapScreen);

export default ElementMapScreen;


