import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import { _fetchAllList as  _fetchScenicList } from '../scenic/listAction';
import { _fetchAllList as  _fetchHotelList } from '../hotel/listAction';
import { _fetchAllList as  _fetchFoodList } from '../restaurant/listAction';
import { _fetchAllList as  _fetchShoppingList } from '../shopping/listAction';
import { _fetchAllList as  _fetchFunList } from '../fun/listAction';

export function goBackAction()
{
    return {
        type: IConstant.Actions.MAP.ELEMENTMAP.GO_BACK,
    };
}

export function isPanelShowingChangeAction(isPanelShowing)
{
    return {
        type: IConstant.Actions.MAP.ELEMENTMAP.IS_PANEL_SHOWING_CHANGE,
        isPanelShowing: isPanelShowing,
    };
}

export function isFetchingChangeAction(isFetching)
{
    return {
        type: IConstant.Actions.MAP.ELEMENTMAP.IS_FETCHING_CHANGE,
        isFetching:isFetching
    };
}

export function keywordChangeAction(keywords)
{
    return function(dispatch, getState)
    {
        dispatch(setKeywordsAction(keywords));
        return new Promise((resolve)=>{
            resolve();
        });
    };
}

function setKeywordsAction(keywords)
{
    return {
        type: IConstant.Actions.MAP.ELEMENTMAP.KEYWORDS_CHANGE,
        keywords: keywords,
    };
}

export function mapZoomAndCenterLocationChangeAction(mapZoom,centerLat,centerLng)
{
    return function(dispatch, getState)
    {
        dispatch(_mapZoomAndCenterLocationChangeAction(mapZoom,centerLat,centerLng));
        return new Promise((resolve)=>{
            resolve();
        });
    };
    
}

function _mapZoomAndCenterLocationChangeAction(mapZoom,centerLat,centerLng)
{
    return {
        type: IConstant.Actions.MAP.ELEMENTMAP.MAPZOOM_AND_CENTERLOCATION_CHANGE,
        mapZoom:mapZoom,
        centerLat: centerLat,
        centerLng: centerLng
    };
}

export function searchTypeChangeAction(searchType)
{
    return {
        type: IConstant.Actions.MAP.ELEMENTMAP.SEARCHTYPE_CHANGE,
        searchType: searchType,
    };
}

export function asyncGetListAction(searchType, keywords,lat,lng)
{
    return function(dispatch, getState)
    {
        dispatch(_getListRequestAction(searchType, keywords,lat,lng));
        return _fetchList(searchType, keywords,lat,lng).then((resp)=>{
            dispatch(_getListSuccessAction(searchType, keywords,lat,lng, resp));
        }).catch((error)=>{
            dispatch(_getListFailureAction(searchType, keywords,lat,lng, error));
        });
    };
}

export function selectedItemChangeAction(selectedItem)
{
    return {
        type: IConstant.Actions.MAP.ELEMENTMAP.SELECTEDITEM_CHANGE,
        selectedItem: selectedItem,
    };
}

function _fetchList(searchType, keywords,lat,lng)
{
    if (searchType === IConstant.ItemType.You)
    {
        return _fetchScenicList(keywords,lat,lng);
    }
    else if (searchType === IConstant.ItemType.Zhu)
    {
        return _fetchHotelList(keywords,lat,lng);
    }
    else if (searchType === IConstant.ItemType.CanTing)
    {
        return _fetchFoodList(keywords,lat,lng);
    }
    else if (searchType === IConstant.ItemType.Gou)
    {
        return _fetchShoppingList(keywords,lat,lng);
    }
    else if (searchType === IConstant.ItemType.Yu)
    {
        return _fetchFunList(keywords,lat,lng);
    }
    else
    {
        return _fetchEntityList(searchType,keywords,lat,lng);
    }
}

function _fetchEntityList(searchType, keywords,lat,lng)
{
    let PortalApis;
    let kvDict = {
        conditionParams: {
            Page: {
                PageIndex: 1,
                PageSize: IConstant.ALL_PAGE_SIZE
            },
            Search: [],
            Order: []
        },
        CityCode: IConstant.CITY_CODE,
        lat: lat,
        lng: lng
    };
    if (searchType === IConstant.ItemType.Toilet)
    {
        PortalApis = IConstant.PortalApis.COMMON_SERVICE.ToiletList;
    }
    else if (searchType === IConstant.ItemType.Bank)
    {
        PortalApis = IConstant.PortalApis.COMMON_SERVICE.BankList;
    }
    else if (searchType === IConstant.ItemType.Hopsital)
    {
        PortalApis = IConstant.PortalApis.COMMON_SERVICE.HospitalList;
    }
    else if (searchType === IConstant.ItemType.PostOffice)
    {
        PortalApis = IConstant.PortalApis.COMMON_SERVICE.PostOfficeList;
    }
    else if (searchType === IConstant.ItemType.Gasstation)
    {
        PortalApis = IConstant.PortalApis.COMMON_SERVICE.GasstationList;
    }
    return HttpClient.getFromPortal(PortalApis,kvDict)
        .then((resp) => resp.json());
}

function _getListRequestAction(searchType, keywords,lat,lng)
{
    return {
        type: IConstant.Actions.MAP.ELEMENTMAP.GET_LIST_REQUEST,
        searchType: searchType,
        keywords: keywords,
    };
}

function _getListSuccessAction(searchType, keywords,lat,lng,resp)
{
    return {
        type: IConstant.Actions.MAP.ELEMENTMAP.GET_LIST_SUCCESS,
        searchType: searchType,
        keywords: keywords,
        result:resp
    };
}

function _getListFailureAction(searchType, keywords,lat,lng, error)
{
    return {
        type: IConstant.Actions.MAP.ELEMENTMAP.GET_LIST_FAILURE,
        searchType: searchType,
        keywords: keywords,
        message: error
    };
}


